<template>
    <div class="wx">
    </div>
</template>

<script>
    import wx from 'weixin-js-sdk';         // sdk
    import {Toast} from 'mint-ui';

    export default {
        name: "wxconfig",
        props: ['currentUrl'],
        data() {
            return {
                url: '',
                currentDataUrl: ''
            }
        },
        beforeRouteEnter(to, from, next) {
            next()
        },
        mounted() {
//            let self = this
//            self.setConfig()
        },
        methods: {
            // sdk
            setConfig() {
                let u = navigator.userAgent
                let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
                // location.assign(this.currentUrl)
                if (isIOS) {
                    // this.url = sessionStorage.getItem('sessionUrl')
                    this.url = this.currentDataUrl?this.currentDataUrl:window.location.href
                } else {
                    this.url = this.currentDataUrl?this.currentDataUrl:window.location.href
                }
                this.$post('wachat/getWachatSdk', {
                    url: encodeURIComponent(this.url)
                })
                    .then(res => {
                        wx.config({
                            debug: false, // ,apialertpclogpc
                            appId: res.js_sdk.appId, // 
                            timestamp: res.js_sdk.timestamp, // 
                            nonceStr: res.js_sdk.nonceStr, // 
                            signature: res.js_sdk.signature,// 
                            jsApiList: res.js_sdk.jsApiList // JS
                        })

                    })
            }
        },
        watch: {
            currentUrl(val) {
                this.currentDataUrl = val
            }
        }
    }
</script>

<style scoped>

</style>



